import React, { Component } from 'react'
import { Banners, Slider, Banner } from '../../components'
import {BestProducts, Products} from '../../containers'

class MainGrid extends Component {
  render(){
    return(
        <div>
            <Slider />
            <Banners />
            <Products />
            <Banner />
            <BestProducts />
        </div>
    );
  }
}

export default MainGrid;
