/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faShoppingCart} from '@fortawesome/free-solid-svg-icons'

import {addToBasket} from '../../actions'



class Product extends Component {
    notify = () => toast.success(`    ${this.props.data.name}`, {
        position: "top-left",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true
        });

    onBthClick = () => {
        this.notify()
        this.props.addToBasket(this.props.data._id,this.props.products)
    }

    render(){
        return(
            <div>
                <div className={'product-img'}>
                    <Link to={`/${this.props.data._id}`}>
                         <img src={this.props.data.mainImage.smallImage} alt={this.props.data.mainImage.alt}/>
                    </Link>
                    <div className={'product-action'}>
                        <div className={'pro-action-left'}>
                            <Link to="#" title={'Add to Cart'} onClick={this.onBthClick}>
                                <FontAwesomeIcon icon={faShoppingCart} size="lg" className={'mr-10'}/> Add to Cart
                            </Link>
                        </div>
                    </div>
                </div>
                <div className={'product-content'}>
                    <h4>
                        <Link to={`/${this.props.data._id}`}>{this.props.data.name}</Link>
                    </h4>
                    <div className={'product-price-wrapper'}>
                    {this.props.data.stockPrice <= 0 ? (<span>{this.props.data.price} </span>) : (<span>{this.props.data.stockPrice} </span>)} 
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        products: store.productsReducer.products,
    }
}

const mapDispatchToProps = {
    addToBasket
}
  

export default connect(mapStateToProps,mapDispatchToProps)(Product)
  
  