/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import InputMask from 'react-input-mask'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faArrowUp} from '@fortawesome/free-solid-svg-icons'

import {userUpdate, userStatusRefresh} from '../../actions'



class MyAccount extends Component {

    state={
        id: this.props.user.id,
        name: this.props.user.name,
        login: this.props.user.login,
        email: this.props.user.email,
        phone: this.props.user.phone,
        city: this.props.user.city,

        userStatus: Boolean
    }

    componentDidUpdate(prevProps) {
        if (this.props.updateStatus !== prevProps.updateStatus) {
            this.setState({ userStatus: this.props.updateStatus})
        }
    }

    change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
    };

    renderNotify = () => {
        if(this.state.userStatus === true){
            toast.success(`     `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({userStatus: Boolean})
            this.props.userStatusRefresh()
        }
        else if(this.state.userStatus === false){
            toast.error(`  ,   `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({userStatus: Boolean})
            this.props.userStatusRefresh()
        }
    }

    onBthClick = () => {
        const user = {
            id: this.state.id,
            name: this.state.name,
            login: this.state.login,
            email: this.state.email,
            phone: this.state.phone,
            city: this.state.city,
        }
        this.props.userUpdate(user)
    }

    render(){
        return(
            <div>
                {this.renderNotify()}
                <div className={'myaccount-area pb-80 pt-100'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'ml-auto mr-auto col-lg-9'}>
                        <div className={'checkout-wrapper'}>
                            <div id={'faq'} className={'panel-group'}>
                                <div className={'panel panel-default'}>
                                    <div className={'panel-heading'}>
                                        <h5 className={'panel-title'}><span>1</span> <a data-toggle={'collapse'} data-parent={'#faq'} href={'#my-account-1'}>    </a></h5>
                                    </div>
                                    <div id={'my-account-1'} className={'panel-collapse collapse show'}>
                                        <div className={'panel-body'}>
                                            <div className={'billing-information-wrapper'}>
                                                <div className={'account-info-wrapper'}>
                                                    <h4>  </h4>
                                                    <h5>  </h5>
                                                </div>
                                                <div className={'row'}>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label></label>
                                                            <input onChange={e => this.change(e)} type={'text'} value={this.state.name} name={'name'}/>
                                                        </div>
                                                    </div>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label></label>
                                                            <input onChange={e => this.change(e)} type={'text'} value={this.state.login} name={'login'}/>
                                                        </div>
                                                    </div>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label></label>
                                                            <input onChange={e => this.change(e)} type={'email'} value={this.state.email} name={'email'}/>
                                                        </div>
                                                    </div>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label></label>
                                                            <InputMask onChange={e => this.change(e)} value={this.state.phone} name={'phone'} placeholder={'*'} mask="+7 (999) 999-99-99" maskChar={null} />
                                                        </div>
                                                    </div>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label></label>
                                                            <input onChange={e => this.change(e)} type={'text'} value={this.state.city} name={'city'}/>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={'billing-back-btn'}>
                                                    <div className={'billing-back'}>
                                                        <Link to="/"><FontAwesomeIcon icon={faArrowUp}/> </Link>
                                                    </div>
                                                    <div className={'billing-btn'}>
                                                        <button onClick={this.onBthClick}></button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className={'panel panel-default'}>
                                    <div className={'panel-heading'}>
                                        <h5 className={'panel-title'}><span>2</span> <a data-toggle={'collapse'} data-parent={'#faq'} href={'#my-account-2'}>  </a></h5>
                                    </div>
                                    <div id={'my-account-2'} className={'panel-collapse collapse'}>
                                        <div className={'panel-body'}>
                                            <div className={'billing-information-wrapper'}>
                                                <div className={'account-info-wrapper'}>
                                                    <h4> </h4>
                                                    <h5> </h5>
                                                </div>
                                                <div className={'row'}>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label> </label>
                                                            <input type={'password'} name={'password'}/>
                                                        </div>
                                                    </div>
                                                    <div className={'col-lg-12 col-md-12'}>
                                                        <div className={'billing-info'}>
                                                            <label> </label>
                                                            <input type={'password'} name={'confirmPassword'}/>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={'billing-back-btn'}>
                                                    <div className={'billing-back'}>
                                                        <a data-toggle={'collapse'} data-parent={'#faq'} href={'#my-account-1'}><FontAwesomeIcon icon={faArrowUp}/> </a>
                                                    </div>
                                                    <div className={'billing-btn'}>
                                                        <button type={'submit'}></button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
            </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        user: store.userReducer.user,
        updateStatus: store.userReducer.updateStatus
    }
}

const mapDispatchToProps = {
    userUpdate,
    userStatusRefresh
}
  

export default connect(mapStateToProps,mapDispatchToProps)(MyAccount)
  
  