import React, {Component} from 'react'
import { connect } from 'react-redux'
import { sendOrder, cleanBasket } from '../../actions'
import { toast } from 'react-toastify'
import InputMask from 'react-input-mask'

import 'react-toastify/dist/ReactToastify.css'


class PaymentGrid extends Component {
    state = {
        name: "",
        nameError: "",
        email: "",
        emailError: "",
        phone: "",
        phoneError: "",
        city: "",
        cityError: "",

        user: this.props.user,
        error: Boolean,
        notifyStatus: Boolean
    };
    
    change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
    };

    componentDidUpdate(prevProps) {
        if (this.props.status !== prevProps.status) {
          this.setState({notifyStatus: this.props.status})
        }
    }

    validate = () => {
    
        if (this.state.name <= 1) {
          this.setState({error: true})
          this.setState({nameError: "  "})
        }
        else{
          this.setState({error: false})
          this.setState({nameError: ""})
        }

        if(this.state.email.indexOf("@") === -1) {
            this.setState({error: true})
            this.setState({emailError: "  email"})
          }
        else{
            this.setState({error: false})
            this.setState({emailError: ""})
        }
        if(this.state.phone.length < 10){
            this.setState({error: true})
            this.setState({phoneError: "  "})
        }
        else{
            this.setState({error: false})
            this.setState({phoneError: ""})
        }
    };
    
    onSubmit = e => {
        e.preventDefault();
    
        this.validate();
        if (!this.state.error) {
            this.props.sendOrder(this.state.name, this.state.email, this.state.city, this.state.phone, this.props.totalBasketPrice, this.props.basketProducts)
            this.props.cleanBasket()
        this.setState({
            name: "",
            nameError: "",
            email: "",
            emailError: "",
            phone: "",
            phoneError: "",
            city: "",
            cityError: "",

            error: Boolean
          });
      }
    }

    renderNotify = () => {
        if(this.state.notifyStatus === true){
            toast.success(`     `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({notifyStatus: Boolean})
        }
        else if(this.state.notifyStatus === false){
            toast.error(`  ,   `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({notifyStatus: Boolean})
        }
    }

    render(){
        return(
         <div>
             {this.renderNotify()}
            <div className={'contact-area pt-100 pb-100'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-12'}>
                        <div className={'contact-message-wrapper'}>
                            <h4 className={'contact-title'}>  </h4>
                            <div className={'contact-message'}>
                                <form id={'contact-form'}>
                                    <div className={'row'}>
                                        <div className={'col-lg-12'}>
                                          {this.state.nameError !== "" ? (<p className={'error'}>{this.state.nameError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <input onChange={e => this.change(e)} value={this.state.name} name={'name'} placeholder={'*'} type={'text'}/>
                                            </div>
                                        </div>
                                        <div className={'col-lg-12'}>
                                          {this.state.emailError !== "" ? (<p className={'error'}>{this.state.emailError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <input onChange={e => this.change(e)} value={this.state.email} name={'email'} placeholder={'.*'} type={'email'}/>
                                            </div>
                                        </div>
                                        <div className={'col-lg-12'}>
                                          {this.state.phoneError !== "" ? (<p className={'error'}>{this.state.phoneError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <input onChange={e => this.change(e)} value={this.state.city} name={'city'} placeholder={'*'} type={'text'}/>
                                            </div>
                                        </div>
                                        <div className={'col-lg-12'}>
                                          {this.state.cityError !== "" ? (<p className={'error'}>{this.state.cityError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <InputMask onChange={e => this.change(e)} value={this.state.phone} name={'phone'} placeholder={''} mask="+7 (999) 999-99-99" maskChar={null} />
                                                <button onClick={e => this.onSubmit(e)} className={'submit btn-style'} type={'submit'}> </button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
     </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        status: store.userReducer.orderStatus,
        basketProducts: store.cartReducer.basketProducts,
        totalBasketPrice: store.cartReducer.totalBasketPrice
    }
  }
  
  const mapDispatchToProps = {
    sendOrder,
    cleanBasket
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(PaymentGrid)