import {
    GET_CATEGORIES_SUCCCES,
    GET_CATEGORIES_FAILURE,
    GET_SELECTED_CATEGORIE
} from '../../actionType'

const initialState = {
    categories: [],
    selectedCategorie: "",
    isFetching: false
}

function productsReducer(state = initialState, action) {
    switch (action.type) {
  
      case GET_CATEGORIES_SUCCCES:
        return { ...state, categories: action.payload, isFetching: true, error: '' }
  
      case GET_CATEGORIES_FAILURE:
        return { ...state, error: action.payload.message, isFetching: false }

      case GET_SELECTED_CATEGORIE:
        return{ ...state, selectedCategorie: action.payload, isFetching: true}
  
      default:
        return state
    }
  }
  

export default productsReducer