import { fetchProductApi, fetchProductsApi } from '../../API'

import {GET_PRODUCT_BY_ID_START,
  GET_PRODUCT_BY_ID_SUCCES,
  GET_PRODUCT_BY_ID_FAILURE,
  GET_PRODUCTS_START,
  GET_PRODUCTS_SUCCES,
  GET_PRODUCTS_FAILURE} from '../../actionType'

export const fetchProduct = (id) => async dispatch => {
    dispatch({type: GET_PRODUCT_BY_ID_START})

  try {
    const product = await fetchProductApi(id)
    product.count = 0;
    console.log("ACTION:"+product)
    dispatch({
      type: GET_PRODUCT_BY_ID_SUCCES,
      payload: product
    })
  } catch (err) {
    dispatch({
      type: GET_PRODUCT_BY_ID_FAILURE,
      payload: err,
      error: true
    })
  }
}

export const fetchProducts = () => async dispatch => {
  dispatch({type: GET_PRODUCTS_START})

try {
  const products = await fetchProductsApi()
  console.log("ACTION:"+products)

  dispatch({
    type: GET_PRODUCTS_SUCCES,
    payload: products
  })
} catch (err) {
  dispatch({
    type: GET_PRODUCTS_FAILURE,
    payload: err,
    error: true
  })
}
}

