import React, { Component } from 'react'
import { Link } from 'react-router-dom'


class Footer extends Component {
  render(){
    return(
    <div className={'banner-area'}>
        <div className={'container'}>
            <div className={'discount-overlay bg-img pt-130 pb-130'} style={{backgroundImage: "url(assets/img/banner/banner-4.jpg)"}}>
                <div className={'discount-content text-center'}>
                    <h3>Its Time To Start <br/>Your Own Revolution By Laurent</h3>
                    <p>Exclusive Offer -10% Off This Week</p>
                    <div className={'banner-btn'}>
                      <Link to="/discount">Alert</Link>
                    </div>
                </div>
            </div>
        </div>
    </div>
    );
  }
}

export default Footer;
