import { userSingInApi, userSingUpApi, sendMailApi, orderApi, userAcceptApi, userLogOutApi, userUpdateApi } from '../../API'

import {
SINGIN_USER_START, SINGIN_USER_SUCCES, SINGIN_USER_FAILURE,
SINGUP_USER_START, SINGUP_USER_SUCCES, SINGUP_USER_FAILURE,
USER_ACCEPT_START, USER_ACCEPT_SUCCES, USER_ACCEPT_FAILURE,
SINGOUT_USER_START, SINGOUT_USER_SUCCES, SINGOUT_USER_FAILURE,
USER_UPDATE_START, USER_UPDATE_SUCCES, USER_UPDATE_FAILURE,
SEND_MESSAGE_START, SEND_MESSAGE_SUCCES, SEND_MESSAGE_FAILURE,
ORDER_START, ORDER_SUCCES, ORDER_FAILURE, USER_UPDATE_STATUS_REFRESH
} from '../../actionType'

export const userSingIn = (login, password, isRemember) => async dispatch => {
  dispatch({type: SINGIN_USER_START})

try {
  const packageUser = await userSingInApi(login, password)
  console.log("ACTION: "+ isRemember)
  console.log("ACTION: packageUser "+ packageUser.buffer.redirectStatus)
  if(isRemember === "on" && packageUser.buffer.redirectStatus === true){
    
  }
  dispatch({
    type: SINGIN_USER_SUCCES,
    payload: packageUser
  })
} catch (err) {
  dispatch({
    type: SINGIN_USER_FAILURE,
    payload: err,
    error: true
  })
}
}

export const userSingUp = (name, login, password, email) => async dispatch => {
  dispatch({type: SINGUP_USER_START})

try {
  const redirectStatus = await userSingUpApi(name, login, password, email)

  dispatch({
    type: SINGUP_USER_SUCCES,
    payload: redirectStatus.status
  })
} catch (err) {
  dispatch({
    type: SINGUP_USER_FAILURE,
    payload: err,
    error: true
  })
}
}

export const userAccept = (id) => async dispatch => {
  dispatch({type: USER_ACCEPT_START})

try {
  const packageUser = await userAcceptApi(id)
  console.log("ACTION: " + packageUser)
  dispatch({
    type: USER_ACCEPT_SUCCES,
    payload: packageUser
  })
} catch (err) {
  dispatch({
    type: USER_ACCEPT_FAILURE,
    payload: err,
    error: true
  })
}
}

export const userStatusRefresh = () => dispatch => {
  dispatch({type: USER_UPDATE_STATUS_REFRESH})
}


export const userSingOut = () => async dispatch => {
  dispatch({type: SINGOUT_USER_START})

  try {
    await userLogOutApi()

    dispatch({
      type: SINGOUT_USER_SUCCES,
      payload: ""
    })
  } catch (err) {
    dispatch({
      type: SINGOUT_USER_FAILURE,
      payload: err,
      error: true
    })
  }
}

export const userUpdate = (updatedUser) => async dispatch => {
  dispatch({type: USER_UPDATE_START})

  try{
    const status = await userUpdateApi(updatedUser)
    dispatch({
      type: USER_UPDATE_SUCCES,
      payload: status.status
    })
  }
  catch(err){
    dispatch({
      type: USER_UPDATE_FAILURE,
      payload: err,
      error: true
    })
  }
}

export const sendOrder = (name, email, city, phone, price, products) => async dispatch => {
  dispatch({type: ORDER_START})

try {
  const sendStatus = await orderApi(name, email, city, phone, price, products)
  
  console.log(sendStatus.status)

  dispatch({
    type: ORDER_SUCCES,
    payload: sendStatus.status
  })
} catch (err) {
  dispatch({
    type: ORDER_FAILURE,
    payload: err,
    error: true
  })
}
}

export const sendMail = (email, name, phone, message) => async dispatch => {
  dispatch({type: SEND_MESSAGE_START})

try {
  const sendStatus = await sendMailApi(email, name, phone, message)
  console.log(sendStatus.status)

  dispatch({
    type: SEND_MESSAGE_SUCCES,
    payload: sendStatus.status
  })
} catch (err) {
  dispatch({
    type: SEND_MESSAGE_FAILURE,
    payload: err,
    error: true
  })
}
}
