import React, {Component} from 'react'
import {Link, Redirect} from 'react-router-dom'
import PasswordMask from 'react-password-mask'
import { connect } from 'react-redux'


import { userSingUp } from '../../actions'

class SingUpGrid extends Component{


    state = {
        name:"",
        login: "",
        loginError: "",
        email: "",
        emailError: "",
        password: "",
        passwordError: "",

        error: Boolean
      };
    
      change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
      };
    
      validate = () => {
        console.log(this.state.login)
        
    
        if(this.state.login.length < 5) {
          this.setState({error: true})
          this.setState({loginError: "    5 "})
        }
        else{
          this.setState({error: false})
          this.setState({loginError: ""})
        }
    
        if(this.state.email.indexOf("@") === -1) {
          this.setState({error: true})
          this.setState({emailError: "  email"})
        }
        else{
          this.setState({error: false})
          this.setState({emailError: ""})
        }
    
        const numbers = /[0-9]/g;
    
        if(!this.state.password.match(numbers) && this.state.password.length < 8) {
          this.setState({error: true})
          console.log(this.state.password)
          this.setState({passwordError: "           8 "})
        }
        else{
          this.setState({error: false})
          this.setState({passwordError: ""})
        }
      };
    
      onSubmit = e => {
        e.preventDefault();
    
        this.validate();
        if (!this.state.error) {
            this.props.userSingUp(this.state.name,this.state.login, this.state.password, this.state.email)

            this.setState({
              name: "",
              login: "",
              loginError: "",
              email: "",
              emailError: "",
              password: "",
              passwordError: ""
            });
          }

        }
    
      renderRedirect = () => {
        if (this.props.redirectStatus === true) {
          return <Redirect to='/singIn' />
        }
        else if(this.props.redirectStatus === false && this.state.error === false) {
          return <p className={'error'}>   </p>
        }
      }

    render(){
        return(
        <div className={'login-register-area pt-95 pb-100'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-lg-7 col-md-12 ml-auto mr-auto'}>
                        <div className={'login-register-wrapper'}>
                            <div className={'login-register-tab-list nav'}>
                                <Link to={'/singIn'}>
                                    <h4>  </h4>
                                </Link>
                                <Link className={'active'} to={'/singUp'}>
                                    <h4>  </h4>
                                </Link>
                            </div>
                            <div className={'tab-content'}>
                                <div className={'tab-pane active'}>
                                    <div className={'login-form-container'}>
                                        <div className={'login-register-form'}>
                                            <form>
                                                {this.renderRedirect()}
                                                <input type={'text'} onChange={e => this.change(e)} name="name" value={this.state.name} placeholder={''} />
                                                {this.state.loginError !== "" ? (<p className={'error'}>{this.state.loginError}</p>) : (false)}
                                                <input type={'text'} onChange={e => this.change(e)} name="login" value={this.state.login} placeholder={''} />
                                                {this.state.passwordError !== "" ? (<p className={'error'}>{this.state.passwordError}</p>) : (false)}
                                                <PasswordMask showButtonContent="" hideButtonContent="" onChange={e => this.change(e)} name="password" value={this.state.password} placeholder={''} />
                                                {this.state.emailError !== "" ? (<p className={'error'}>{this.state.emailError}</p>) : (false)}
                                                <input name={'email'} onChange={e => this.change(e)} value={this.state.email} placeholder={'Email'} type={'email'} />
                                                <div className={'button-box'}>
                                                    <button onClick={e => this.onSubmit(e)}><span></span></button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}


const mapStateToProps = store => {
    return {
      redirectStatus: store.userReducer.singUpRedirect
    }
  }
  
  const mapDispatchToProps = {
    userSingUp
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(SingUpGrid)