import React,{Component} from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import { Product } from '../index'
 
class BestProducts extends Component {

    renderTemplate = () => {
        const { products, isFetching } = this.props
        
        if (isFetching === false) {
          return <p>...</p>
        } 
        else {
            return products.map(product => (
                <div className={'product-wrapper'} key={product._id}>
                    <Product data={product}/>
                </div>
          ))
        }
      } 

  render(){
      const {products} = this.props
    return(
      <div className={'best-food-area pt-100 pb-95'}>
          <div className={'custom-container'}>
              <div className={'row'}>
                  <div className={'best-food-width-1'}>
                      <div className={'single-banner'}>
                          <div className={'hover-style'}>
                              <Link to={'#'}><img src={'assets/img/banners/banner-5.jpg'} alt={''}/></Link>
                          </div>
                      </div>
                  </div>
                  <div className={'best-food-width-2'}>
                      <div className={'product-top-bar section-border mb-25'}>
                          <div className={'section-title-wrap'}>
                              <h3 className={'section-title section-bg-white'}> </h3>
                          </div>
                          <div className={'product-tab-list-2 nav section-bg-white'}>
                              <Link className={'active'} to={'#tab4'} data-toggle={'tab'}>
                                  <h4>All </h4>
                              </Link>
                              <Link to={'#tab5'} data-toggle={'tab'}>
                                  <h4>Food </h4>
                              </Link>
                              <Link to={'#tab6'} data-toggle={'tab'}>
                                  <h4> Drink </h4>
                              </Link>
                          </div>
                      </div>
                      <div className={'tab-content jump'}>
                          <div id={'tab4'} className={'tab-pane active'}>
                          <OwlCarousel
                            className={'owl-theme'}
                            loop
                            margin = {30}
                            autoplay= {true}
                            slideBy= {3}
                            items={products.lenght}
                            dots={false}
                            >
                             {this.renderTemplate()}
                              </OwlCarousel>
                          </div>
                  </div>
              </div>
              <div className={'best-food-width-1 mrg-small-35'}>
                  <div className={'single-banner'}>
                      <div className={'hover-style'}>
                          <Link to={'#'}><img src={'assets/img/banners/banner-6.jpg'} alt={''}/></Link>
                      </div>
                  </div>
              </div>
          </div>
      </div>
    </div>
    );
  }
}

const mapStateToProps = store => {
    return {
     products: store.productsReducer.products,
     isFetching: store.productsReducer.isFetching
    }
  }


  export default connect(
    mapStateToProps, null
  )(BestProducts)
