/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from 'react'
import { Markup } from 'interweave'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import ImageZoom from 'react-medium-image-zoom'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faShoppingCart} from '@fortawesome/free-solid-svg-icons'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'


import {fetchProduct, selectedCategorie, addToBasketFromProductDetails} from '../../actions'


class ProductDetails extends Component {

    notify = () => toast.success(`    ${this.props.product.name}, ${this.state.count} `, {
        position: "top-left",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true
    });
    
    constructor(props) {
        super(props);
        this.state = {count: 1};
    
        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount () {
        const url = window.location.href
        const id = url.substring(22)
        this.props.fetchProduct(id)
    }

    onBthClick = () => {
        this.notify()
        this.props.addToBasketFromProductDetails(this.props.product,this.state.count)
    }

    handleChange(event) {
        this.setState({count: event.target.value})
    }

    renderCategories = (categories) =>{
        return categories.map(categorie => <li key={categorie}><Link onClick={this.props.selectedCategorie(categorie)} to={`/shop`}>{categorie + ", "}</Link></li>)
    }

    render(){
        const { product } = this.props
        return(
    <div>
        <div className={'product-details pt-100 pb-90'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-lg-6 col-md-12'}>
                        {/* Zoomable image */}
                        <div className={'product-details-img'}>
                            <ImageZoom
                            image={{
                                src: product.mainImage.image,
                                alt: product.mainImage.alt,
                                className: 'zoompro'
                              }}
                              zoomImage={{
                                src: product.mainImage.zoomImage,
                                alt: product.mainImage.alt
                              }}/>
                              {product.stockPrice <= 0 ? (<div></div>) : (<span>{parseInt(100-((product.stockPrice * 100) / product.price), 10)} %</span>)}
                        </div>
                    </div>
                    <div className={'col-lg-6 col-md-12'}>
                        <div className={'product-details-content'}>
                            {/* ProductName */}
                            <h2>{product.name}</h2>
                            {/* ProductPrice */}
                            {product.stockPrice <= 0 ? (<span>{product.price} </span>) : (
                            <div className={'product-prices-block'}>
                                <span className={'product-price-old'}>{product.price} </span>
                                <br/>
                                <span>{product.stockPrice} </span>
                            </div>
                            )}
                            <div className={'in-stock'}>
                                {/* Avaible in stock */}
                                <p> : <span>{product.isAvaible}</span></p>
                            </div>
                            {/* Short description */}
                            <p>{product.description}</p>
                            <div className={'pro-details-cart-wrap'}>
                                <div className={'shop-list-cart-wishlist'}>
                                    <Link title={'  '} to="#" onClick={this.onBthClick}>
                                        <FontAwesomeIcon icon={faShoppingCart} className={'mb-5'}/>
                                    </Link>
                                </div>
                                <div className={'product-quantity'}>
                                    <div className={'cart-plus-minus'}>
                                        <input className={'cart-plus-minus-box'} min="1" value={this.state.value} 
                                        onChange={this.handleChange} type={'number'} name={'count'} /> 
                                    </div>
                                </div>
                            </div>
                            <div className={'pro-dec-categories'}>
                                <ul>
                                    <li className={'categories-title'}>:</li>
                                    {this.renderCategories(product.categories)}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div className={'description-review-area pb-100'}>
            <div className={'container'}>
                <div className={'description-review-wrapper'}>
                    <div className={'description-review-topbar nav text-center'}>
                        <a className="active" data-toggle="tab" href="#des-details1"></a>
                        <a data-toggle="tab" href="#des-details2"></a>
                    </div>
                    <div className={'tab-content description-review-bottom'}>
                        <div id={'des-details1'} className={'tab-pane active'}>
                            <div className={'product-description-wrapper'}>
                                {/* FullDescription */}
                                <p className={'product-full-description'}>
                                    <Markup content={product.fullDescription}/>  
                                </p>        
                            </div>
                        </div>
                        <div id={'des-details2'} className={'tab-pane'}>
                            <div className={'product-anotherinfo-wrapper'}>
                                <ul>
                                    <li><span>:</span></li>
                                    {this.renderCategories(product.categories)}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
        );
    }
}

const mapStateToProps = store => {
    return {
      product: store.product.product,
      isFetching: store.product.isFetching
    }
  }
  
  const mapDispatchToProps = {
    fetchProduct,
    addToBasketFromProductDetails,
    selectedCategorie
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(ProductDetails)

