import React, { Component } from 'react'
import { connect } from 'react-redux'
import {Link} from 'react-router-dom'

import { fetchProducts } from '../../actions'
import { Product } from '../index'

class Products extends Component{
  state={
    categories:""
  }
  componentDidMount () {
    this.props.fetchProducts()
  }

  renderTemplate = () => {
    const { products, isFetching } = this.props
    if(products.length > 5){
      products.splice(5,products.length);
    }

    if (isFetching === false) {
      return <p>...</p>
    } else {
      return products.map(product => (
    <div className="custom-col-5" key={product._id}>
      <div className={'product-wrapper mb-25'} >
        <Product data={product}/>
    </div>
    </div>
      ))
    }
  } 

  render(){
    return(
      <div className={'product-area pb-70'}>
          <div className={'custom-container'}>
              <div className={'product-tab-list-wrap text-center mb-40'}>
                  <div className={'product-tab-list nav'}>
                      <Link className={'active'} to={'#tab1'} data-toggle={'tab'}>
                          <h4></h4>
                      </Link>
                  </div>
                  <p>  /     </p>
              </div>
              <div className={'tab-content jump'}>
                  <div id={'tab1'} className={'tab-pane active'}>
                      <div className={'row'}>
                          {this.renderTemplate()}              
                      </div>
                  </div>
              </div>
          </div>
      </div>
    );
  }
}

const mapStateToProps = store => {
  return {
    products: store.productsReducer.products,
    isFetching: store.productsReducer.isFetching
  }
}

const mapDispatchToProps = {
  fetchProducts
}

export default connect(
  mapStateToProps, mapDispatchToProps
)(Products)

