import React, {Component} from 'react'
import { Link } from 'react-router-dom'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faTimes} from '@fortawesome/free-solid-svg-icons'

class FullCartProduct extends Component {
    constructor(props) {
        super(props);
        this.state = {count: this.props.data.count};
    
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(event) {
        this.setState({count: event.target.value})
    }
    onBtnClick = () => {
        this.props.removeFunction(this.props.data)
    }
    render(){
        return(
            <tr>
                <td className={'product-thumbnail'}>
                    <Link to={`/${this.props.data._id}`}><img src={this.props.data.mainImage.cartImage} alt={this.props.data.alt}/></Link>
                </td>
                <td className={'product-name'}><Link to={`/${this.props.data._id}`}>{this.props.data.name} </Link></td>
                <td className={'product-price-cart'}><span className={'amount'}>{this.props.data.stockPrice === 0 ? (this.props.data.price) : (this.props.data.stockPrice) }</span></td>
                <td className={'product-quantity'}>
                    <div className={'cart-plus-minus'}>
                        <input className={'cart-plus-minus-box'} type={'text'} name={'qtybutton'} onChange={this.handleChange} defaultValue={this.props.data.count} />
                    </div>
                </td>
                <td className={'product-subtotal'}>{this.props.data.stockPrice === 0 ? (this.props.data.price * this.state.count) : (this.props.data.stockPrice * this.state.count) } </td>
                <td className={'product-remove'}>
                    <Link to="#" onClick={this.onBtnClick}><FontAwesomeIcon  size="3x" icon={faTimes}/></Link>
                </td>
            </tr>
        );
    }
}

export default FullCartProduct

