import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'

class CartProduct extends Component {
  onBthClick = () => {
    this.props.removeFunction(this.props.data)
  }
  render(){
    return(
    <li className={'single-shopping-cart'}>
        <div className={'shopping-cart-img'}>
            <Link to={`/${this.props.data._id}`}><img alt={this.props.data.alt} src={this.props.data.mainImage.cartImage}/></Link>
        </div>
        <div className={'shopping-cart-title'}>
            <h4><Link to={`/${this.props.data._id}`}>{this.props.data.name}</Link></h4>
            <h6>: {this.props.data.count}</h6>
            <span>{this.props.data.stockPrice === 0 ? (this.props.data.price * this.props.data.count) : (this.props.data.stockPrice * this.props.data.count) } </span>
        </div>
        <div className={'shopping-cart-delete'}>
            <Link to="#" onClick={this.onBthClick}><FontAwesomeIcon icon={faTimes}/></Link>
        </div>
    </li>
    );
  }
}

export default CartProduct;
