import React, {Component} from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight, faTh, faListUl } from '@fortawesome/free-solid-svg-icons'

import {onCategorieClick} from '../../selector'
import { fetchProducts, fetchCategories } from '../../actions'
import { Product, ProductList } from '../index'

class ShopGrid extends Component {
    state={
        categori: "",
        gridSwitcher: false
    }

    componentDidMount () {
        this.props.fetchProducts()
        this.props.fetchCategories()
        if(this.props.selectedCategorie !== ""){
            var categoriesPrev = onCategorieClick(this.props.selectedCategorie, this.state.categori);
            this.setState({categori: categoriesPrev}) 
        }
    }
      
    renderCategories = () => {
        const {categories, isCategoriesFetching } = this.props
        if(isCategoriesFetching === false){
            return <p>...</p>
        } 
        else{
            return categories.map(element => (
                <li key={element.idCategorie}><Link to="#" onClick={this.onBtnClick} >{element.name}</Link></li>
            ))
        }
    }
    
    renderTemplate = () => {
        var isCategory = false
        const { products, isFetching } = this.props
        var filteredProducts = [];

        if(this.state.categori === ""){
            filteredProducts = products
        }
        else{
            products.forEach(element => {
                   element.categories.forEach(element2 => {
                       if(element2 === this.state.categori){
                           filteredProducts.push(element)
                           isCategory = true
                       }
                   });
               });
            if(isCategory === false){
                return <h2 className={'text-center'}>    </h2>
            }
        }
        
        if (isFetching === false) {
          return <p>...</p>
        } else {
          return filteredProducts.map(product => (
        <div className={'product-width col-xl-4 col-lg-4 col-md-4 col-sm-6 col-12 mb-30'} key={product._id}>
          <div className={'product-wrapper mb-25'} >
            {this.state.gridSwitcher === true ? (<ProductList data={product}/>) : (<Product data={product}/>)}
          </div>
        </div>
          ))
        }
      } 

    onBtnClick = e => {
        var categoriesPrev = onCategorieClick(e.currentTarget.innerText, this.state.categori);
        this.setState({categori: categoriesPrev})       
        console.log(e.currentTarget)
        e.currentTarget.className = "activCategory"
        console.log(e.currentTarget)

    }

    onSwitchToGridClick = () => {
        this.setState({gridSwitcher: false})
        console.log(this.state.gridSwitcher)
    }

    onSwitchToListClick = () => {
        this.setState({gridSwitcher: true})
        console.log(this.state.gridSwitcher)
    }

    render(){
        return(
        <div className={'shop-page-area pt-100 pb-100'}>
            <div className={'container'}>
                <div className={'row flex-row-reverse'}>
                    <div className={'col-lg-9'}>
                        <div className={'shop-topbar-wrapper'}>
                            <div className={'shop-topbar-left'}>
                                <ul className={'view-mode'}>
                                {this.state.gridSwitcher === true ? (
                                <div>
                                    <li onClick={this.onSwitchToGridClick} ><Link to={'#'}><FontAwesomeIcon icon={faTh}/></Link></li>
                                    <li onClick={this.onSwitchToListClick} className={'active'}><Link to={'#'}><FontAwesomeIcon icon={faListUl}/></Link></li>
                                </div>) : (
                                <div>
                                    <li onClick={this.onSwitchToGridClick} className={'active'}><Link to={'#'}><FontAwesomeIcon icon={faTh}/></Link></li>
                                    <li onClick={this.onSwitchToListClick} ><Link to={'#'}><FontAwesomeIcon icon={faListUl}/></Link></li>
                                </div>)}
                                    
                                </ul>
                                <p> 1 - 14  14  </p>
                            </div>
                            <div className={'product-sorting-wrapper'}>
                                <div className={'product-shorting shorting-style'}>
                                    <label>View:</label>
                                    <select>
                                        <option value={''}> 20</option>
                                        <option value={''}> 23</option>
                                        <option value={''}> 30</option>
                                    </select>
                                </div>
                                <div className={'product-show shorting-style'}>
                                    <label> :</label>
                                    <select>
                                        <option value={''}> </option>
                                        <option value={''}> </option>
                                        <option value={''}> </option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div className={'grid-list-product-wrapper'}>
                        {this.state.gridSwitcher === true ? (
                            <div className={'product-list product-view pb-20'}>
                                <div className={'row'}>
                                    <div className={'divNoneStyle'}>
                                        {this.renderTemplate()}
                                    </div>                          
                                </div>
                            </div>) : (
                            <div className={'product-grid product-view pb-20'}>
                                <div className={'row'}>
                                    <div className={'divNoneStyle'}>
                                        {this.renderTemplate()}
                                    </div>                          
                                </div>
                            </div>)}       
                        </div>
                        <div className={'pagination-total-pages'}>
                                <div className={'pagination-style'}>
                                    <ul>
                                        <li><Link className={'prev-next prev'} to="/"><FontAwesomeIcon className={'mr-10'} icon={faChevronLeft}/> </Link></li>
                                        <li><Link className={'active'} to="/">1</Link></li>
                                        <li><Link to="/">2</Link></li>
                                        <li><Link to="/">3</Link></li>
                                        <li><Link to="/">...</Link></li>
                                        <li><Link to="/">10</Link></li>
                                        <li><Link className={'prev-next next'} to="/"><FontAwesomeIcon className={'ml-10'} icon={faChevronRight}/></Link></li>
                                    </ul>
                                </div>
                                <div className={'total-pages'}>
                                    <p> 1 - 14  14 </p>
                                </div>
                        </div>
                    </div>
                    <div className={'col-lg-3'}>
                        <div className={'shop-sidebar-wrapper gray-bg-7 shop-sidebar-mrg'}>
                            <div className={'shop-widget mt-40 shop-sidebar-border pt-35'}>
                                <h4 className={'shop-sidebar-title'}></h4>
                                <div className={'shop-tags mt-25'}>
                                    <ul>
                                       {this.renderCategories()}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
            </div>
        </div>
     </div>
        );
    }
}
const mapStateToProps = store => {
    return {
      products: store.productsReducer.products,
      isFetching: store.productsReducer.isFetching,
      categories: store.categoriesReducer.categories,
      selectedCategorie: store.categoriesReducer.selectedCategorie,
      isCategoriesFetching: store.categoriesReducer.isFetching
    }
  }
  
  const mapDispatchToProps = {
    fetchProducts,
    fetchCategories
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(ShopGrid)
  
  