import * as R from 'ramda'

import {
  ADD_TO_CART,
  ADD_TO_CART_FROM_DETAILS,
  REMOVE_FROM_CART,
  UPDATE_AMOUNT,
  CLEAN_BASKET
} from '../../actionType'

const initialState = {
  basketProducts: [],
  updatedPrices: [],
  totalBasketPrice: 0,
  productsCount: 0,
  isHave: false
}

function cartReducer(state = initialState, {type, payload}){
  switch (type) {
//-----------------------------------------------------------------------------------       
    case ADD_TO_CART:
      var isHave = false;
        state.basketProducts.forEach(element => {
          if(element._id === payload._id){
            element.count = parseInt(element.count, 10)
            element.count += 1
            state.productsCount += 1
            //   

            if(payload.stockPrice === 0){
              state.totalBasketPrice += payload.price
              }
            else {
              state.totalBasketPrice += payload.stockPrice
            } 

            isHave = true
          }
        });
      if(isHave === false){
        payload.count = parseInt(payload.count, 10)
        payload.count = 1

        state.productsCount += 1
        if(payload.stockPrice === 0){
          state.totalBasketPrice += payload.price
          }
        else {
          state.totalBasketPrice += payload.stockPrice
        }
        state.basketProducts = R.append(payload, state.basketProducts)
      }
      return { ...state, isHave: true }
//-----------------------------------------------------------------------------------       

    case ADD_TO_CART_FROM_DETAILS:
      var isLoad = false
      state.basketProducts.forEach(element => {
        if(element._id === payload.basketProduct._id){
          payload.basketCount = parseInt(payload.basketCount, 10)
          state.productsCount += payload.basketCount

          element.count += payload.basketCount


          if(element.stockPrice === 0){
            state.totalBasketPrice += (payload.basketProduct.price * payload.basketCount)
          }
          else {
            state.totalBasketPrice += (payload.basketProduct.stockPrice * payload.basketCount)
          }

          isLoad = true
        }
      })
      if(isLoad === false){
        payload.basketCount = parseInt(payload.basketCount, 10)
        state.productsCount += payload.basketCount
        payload.basketProduct.count = payload.basketCount

        if(payload.basketProduct.stockPrice === 0){
          state.totalBasketPrice += (payload.basketProduct.price * payload.basketCount)
        }
        else {
          state.totalBasketPrice += (payload.basketProduct.stockPrice * payload.basketCount)
        }

        state.basketProducts = R.append(payload.basketProduct, state.basketProducts)
      }
      return { ...state, isHave: true }
//-----------------------------------------------------------------------------------    
    case REMOVE_FROM_CART:
        state.basketProducts.forEach(element => {
          if(element._id === payload._id){

            if(payload.stockPrice === 0){
              state.totalBasketPrice -= (payload.count * payload.price)
            }
            else {
              state.totalBasketPrice -= (payload.count * payload.stockPrice)
            }

            state.productsCount -= payload.count
          }
        })
      state.basketProducts = R.without(R.of(payload), state.basketProducts)
      return { ...state, isHave: true }
//-----------------------------------------------------------------------------------        
    case UPDATE_AMOUNT:
      var newProductPrice
      if(payload.updatedProduct.stockPrice === 0){
        newProductPrice = payload.updatedProduct.price * payload.updatedCount
      }
      else{
        newProductPrice = payload.updatedProduct.stockPrice * payload.updatedCount
      }
      state.updatedPrices.push(newProductPrice);

      return { ...state, isHave: true}
//-----------------------------------------------------------------------------------  
    case CLEAN_BASKET:
      state.basketProducts = payload
      state.totalBasketPrice = 0
      state.productsCount = 0
      return { ...state, isHave: false }
    default:
      return state
  }
}

export default cartReducer;