import {
    GET_PRODUCT_BY_ID_SUCCES,
    GET_PRODUCT_BY_ID_FAILURE
} from '../../actionType'

const initialState = {
    product:{
        name: "",
        price: Number,
        stockPrice: Number,
        description: "",
        fullDescription: "",
        isAvaible: "",
        mainImage:{
          image: "",
          smallImage: "",
          cartImage: "",
          alt: "",
          zoomImage: ""
        },
        count: Number,
        categories: []
    },
    isFetching: false
}

function productReducer(state = initialState, action) {
    switch (action.type) {
  
      case GET_PRODUCT_BY_ID_SUCCES:
        return { ...state, product: action.payload, isFetching: true, error: '' }
  
      case GET_PRODUCT_BY_ID_FAILURE:
        return { ...state, error: action.payload.message, isFetching: false }
  
      default:
        return state
    }
  }
  

export default productReducer