import React, { Component } from 'react'
import {Link} from 'react-router-dom'

class Banners extends Component {
  render(){
    return(
      <div className={'banner-area row-col-decrease pt-100 pb-75 clearfix'}>
          <div className={'container'}>
              <div className={'banner-left-side mb-20'}>
                  <div className={'single-banner'}>
                      <div className={'hover-style'}>
                            <img src={'assets/img/banners/banner1.jpg'} alt={''}/>
                      </div>
                  </div>
              </div>
              <div className={'banner-right-side mb-20'}>
                  <div className={'single-banner mb-20'}>
                      <div className={'hover-style'}>
                          <Link to={'/'}><img src={'assets/img/banners/banner-2.jpg'} alt={''}/></Link>
                      </div>
                  </div>
                  <div className={'single-banner'}>
                      <div className={'hover-style'}>
                          <Link to={'/'}><img src={'assets/img/banners/banner-3.jpg'} alt={''}/></Link>
                      </div>
                  </div>
              </div>
          </div>
      </div>
    );
  }
}

export default Banners;
