import React, {Component} from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { sendMail } from '../../actions'
import { toast } from 'react-toastify'
import InputMask from 'react-input-mask'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMapMarkerAlt, faPhone, faEnvelope } from '@fortawesome/free-solid-svg-icons'

import 'react-toastify/dist/ReactToastify.css'


class Contacts extends Component {
    state = {
        name: "",
        nameError: "",
        email: "",
        emailError: "",
        phone: "",
        phoneError: "",
        message: "",

        error: Boolean,
        notifyStatus: Boolean
    };
    
    change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
    };

    componentDidUpdate(prevProps) {
        if (this.props.status !== prevProps.status) {
          this.setState({notifyStatus: this.props.status})
        }
    }

    validate = () => {
    
        if (this.state.name <= 1) {
          this.setState({error: true})
          this.setState({nameError: "  "})
        }
        else{
          this.setState({error: false})
          this.setState({nameError: ""})
        }

        if(this.state.email.indexOf("@") === -1) {
            this.setState({error: true})
            this.setState({emailError: "  email"})
          }
        else{
            this.setState({error: false})
            this.setState({emailError: ""})
        }
        if(this.state.phone.length < 10){
            this.setState({error: true})
            this.setState({phoneError: "  "})
        }
        else{
            this.setState({error: false})
            this.setState({phoneError: ""})
        }
    };
    
    onSubmit = e => {
        e.preventDefault();
    
        this.validate();
        if (!this.state.error) {
            this.props.sendMail(this.state.email, this.state.name, this.state.phone, this.state.message)
        this.setState({
            name: "",
            nameError: "",
            email: "",
            emailError: "",
            phone: "",
            phoneError: "",
            message: "",

            error: Boolean
          });
      }
    }

    renderNotify = () => {
        if(this.state.notifyStatus === true){
            toast.success(`   `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({notifyStatus: Boolean})
        }
        else if(this.state.notifyStatus === false){
            toast.error(`  ,   `, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
            this.setState({notifyStatus: Boolean})
        }
    }

    render(){
        return(
         <div>
             {this.renderNotify()}
            <div className={'contact-area pt-100 pb-100'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-lg-4 col-md-6 col-12'}>
                        <div className={'contact-info-wrapper text-center mb-30'}>
                            <div className={'contact-info-icon'}>
                                <FontAwesomeIcon icon={ faMapMarkerAlt } size="3x"/>
                            </div>
                            <div className={'contact-info-content'}>
                                <h4></h4>
                                <p>, .  40</p>
                                <p>010000</p>
                            </div>
                        </div>
                    </div>
                    <div className={'col-lg-4 col-md-6 col-12'}>
                        <div className={'contact-info-wrapper text-center mb-30'}>
                            <div className={'contact-info-icon'}>
                                <FontAwesomeIcon icon={ faPhone } size="3x"/>
                            </div>
                            <div className={'contact-info-content'}>
                                <h4>   </h4>
                                <p>: 8 (717) 225-26-43</p>
                                <p>: 8 (708) 425 26 43</p>
                            </div>
                        </div>
                    </div>
                    <div className={'col-lg-4 col-md-6 col-12'}>
                        <div className={'contact-info-wrapper text-center mb-30'}>
                            <div className={'contact-info-icon'}>
                                <FontAwesomeIcon icon={ faEnvelope } size="3x"/>
                            </div>
                            <div className={'contact-info-content'}>
                                <h4></h4>
                                <p><Link to="/">info@kumdang-2.kz</Link></p>
                                <p><Link to="/">info@kumdang-2.kz</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={'row'}>
                    <div className={'col-12'}>
                        <div className={'contact-message-wrapper'}>
                            <h4 className={'contact-title'}>  </h4>
                            <div className={'contact-message'}>
                                <form id={'contact-form'}>
                                    <div className={'row'}>
                                        <div className={'col-lg-6'}>
                                          {this.state.nameError !== "" ? (<p className={'error'}>{this.state.nameError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <input onChange={e => this.change(e)} value={this.state.name} name={'name'} placeholder={'*'} type={'text'}/>
                                            </div>
                                        </div>
                                        <div className={'col-lg-6'}>
                                          {this.state.emailError !== "" ? (<p className={'error'}>{this.state.emailError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <input onChange={e => this.change(e)} value={this.state.email} name={'email'} placeholder={'E-mail*'} type={'email'}/>
                                            </div>
                                        </div>
                                        <div className={'col-lg-12'}>
                                          {this.state.phoneError !== "" ? (<p className={'error'}>{this.state.phoneError}</p>) : (false)}
                                            <div className={'contact-form-style mb-20'}>
                                                <InputMask onChange={e => this.change(e)} value={this.state.phone} name={'phone'} placeholder={'*'} mask="+7 (999) 999-99-99" maskChar={null} />
                                            </div>
                                        </div>
                                        <div className={'col-lg-12'}>
                                            <div className={'contact-form-style'}>
                                                <textarea onChange={e => this.change(e)} value={this.state.message} name={'message'} placeholder={' '}></textarea>
                                                <button onClick={e => this.onSubmit(e)} className={'submit btn-style'} type={'submit'}> </button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
     </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        status: store.userReducer.sendStatus
    }
  }
  
  const mapDispatchToProps = {
    sendMail
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(Contacts)