import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'

import { FullCartProduct } from '../../components'
import { cleanBasket, removeFromBasket, addToBasketFromProductDetails } from '../../actions'

class CartGrid extends Component {

    
    renderTemplate = () => {
        const { basketProducts, isHave } = this.props
    
        if (isHave === false) {
          return 
        } else {
            
          return basketProducts.map(product => (
            <FullCartProduct key={product._id} data={product} changeCount={addToBasketFromProductDetails} removeFunction={this.props.removeFromBasket}/>
          ))
        }
    }

    render(){
        return(
            <div className={'cart-main-area pt-95 pb-100'}>
            <div className={'container'}>
                <h3 className={'page-title'}> </h3>
                <div className={'row'}>
                    <div className={'col-lg-12 col-md-12 col-sm-12 col-12'}>
                        <form action={'#'}>
                            <div className={'table-content table-responsive'}>
                                <table>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th> </th>
                                            <th>  </th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.renderTemplate()}
                                    </tbody>
                                </table>
                            </div>
                            <div className={'row'}>
                                <div className={'col-lg-12'}>
                                    <div className={'cart-shiping-update-wrapper'}>
                                        <div className={'cart-shiping-update'}>
                                            <Link to="/shop"> </Link>
                                        </div>
                                        <div className={'cart-clear'}>
                                            <Link to="#" onClick={this.props.cleanBasket}> </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                        <div className={'row'}>
                            <div className={'col-lg-4 col-md-6'}>
                                
                            </div>
                            <div className={'col-lg-4 col-md-6'}>
                              
                            </div>
                            <div className={'col-lg-4 col-md-12'}>
                                <div className={'grand-totall'}>
                                    <div className={'title-wrap'}>
                                        <h4 className={'cart-bottom-title section-bg-gary-cart'}> </h4>
                                    </div>
                                    <h4 className={'grand-totall-title'}>   <span>{this.props.totalBasketPrice} </span></h4>
                                    <Link to="/payment">  </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        basketProducts: store.cartReducer.basketProducts,
        totalBasketPrice: store.cartReducer.totalBasketPrice,
        isHave: store.cartReducer.isHave
    }
}

const mapDispatchToProps = {
    cleanBasket,
    removeFromBasket,
    addToBasketFromProductDetails
}
  
export default connect(mapStateToProps,mapDispatchToProps)(CartGrid)