import { fetchCategoriesApi } from '../../API'
import {GET_CATEGORIES_START, GET_CATEGORIES_SUCCCES, GET_CATEGORIES_FAILURE, GET_SELECTED_CATEGORIE} from '../../actionType'

export const fetchCategories = () => async dispatch => {
    dispatch({type: GET_CATEGORIES_START})

  try {
    const categories = await fetchCategoriesApi()
    console.log("ACTION:"+categories)
  
    dispatch({
      type: GET_CATEGORIES_SUCCCES,
      payload: categories
    })
  } catch (err) {
    dispatch({
      type: GET_CATEGORIES_FAILURE,
      payload: err,
      error: true
    })
  }
}

export const selectedCategorie = categorie => dispatch => {
  dispatch({
    type: GET_SELECTED_CATEGORIE,
    payload: categorie
  })
}

