import { AboutUsGrid, StockGrid, DeliveryGrid} from './components'
import { Contacts, CartGrid, ShopGrid, SingInGrid, SingUpGrid, MyAccount, PaymentGrid, ProductDetails} from './containers'
import { MainGrid } from './views'

const dashboardRoutes = [
    {
        path: "main",
        layout: "/",
        name: "MainPage",
        component: MainGrid,
    },
    {
        path: "about",
        layout: "/",
        name: "AboutUs",
        component: AboutUsGrid,
    },
    {
        path: "contacts",
        layout: "/",
        name: "Contacts",
        component: Contacts,
    },
    {
        path: "stock",
        layout: "/",
        name: "Stock",
        component: StockGrid,
    },
    {
        path: "delivery",
        layout: "/",
        name: "Delivery",
        component: DeliveryGrid,
    },
    {
        path: "cart",
        layout: "/",
        name: "Cart",
        component: CartGrid,
    },
    {
        path: "shop",
        layout: "/",
        name: "Shop",
        component: ShopGrid,
    },
    {
        path: "shop/:categorie",
        layout: "/",
        name: "Shop",
        component: ShopGrid,
    },
    {
        path: "singIn",
        layout: "/",
        name: "SingIn",
        component: SingInGrid,
    },
    {
        path: "singUp",
        layout: "/",
        name: "SingUp",
        component: SingUpGrid,
    },
    {
        path: "account",
        layout: "/",
        name: "Account",
        component: MyAccount,
    },
    {
        path: "payment",
        layout: "/",
        name: "Payment",
        component: PaymentGrid,
    },
    {
        path: ":id",
        layout: "/",
        name: "Product",
        component: ProductDetails,
    },
  ];
  
  export default dashboardRoutes;
  