/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingBag } from '@fortawesome/free-solid-svg-icons'
import { removeFromBasket } from '../../actions'
import { CartProduct } from '../../components'

class Cart extends Component {
  state = {
    cart: false
  };

  cartShow = e => {
    if(this.state.cart === false){
    this.setState({cart: true});
    }
    else{
      this.setState({cart: false});
    }
  }

  renderTemplate = () => {
    const { basketProducts, isHave } = this.props

    if (isHave === false) {
      return <p>   </p>
    } else {
        
      return basketProducts.map(product => (
        <CartProduct key={product._id} data={product} removeFunction={this.props.removeFromBasket}/>
      ))
    }
} 


  render(){
    const isLoggedIn = this.state.cart
    return(
      <div className={'header-cart ml-30'} >
          <a onClick={e => this.cartShow(e)}>
              <div className={'header-icon-style'}>
                    <FontAwesomeIcon icon={faShoppingBag} size="3x"/>
                  <span className={'count-style ml-10'}>{this.props.productsCount}</span>
              </div>
              <div className={'cart-text'}>
                  <span className={'digit'}></span>
                  <span className={'cart-digit-bold'}>{this.props.totalBasketPrice} </span>
              </div>
          </a>
          {isLoggedIn ? (<div className={'shopping-cart-content'} style={{display: "block"}}>
              <ul>
                  {this.renderTemplate()}
              </ul>
              <div className={'shopping-cart-total'}>
                  <h4> : <span className={'shop-total'}>{this.props.totalBasketPrice} </span></h4>
              </div>
              <div className={'shopping-cart-btn'}>
                  <Link to={'/cart'}>  </Link>
                  <Link to={'/payment'}></Link>
              </div>
          </div>)
          : (<div></div>)
        }
      </div>
    );
  }
}

const mapStateToProps = store => {
  return {
      basketProducts: store.cartReducer.basketProducts,
      totalBasketPrice: store.cartReducer.totalBasketPrice,
      isHave: store.cartReducer.isHave,
      productsCount: store.cartReducer.productsCount
  }
}

const mapDispatchToProps = {
  removeFromBasket
}

export default connect(mapStateToProps,mapDispatchToProps)(Cart)