import axios from 'axios'
axios.defaults.withCredentials = true

export const fetchProductsApi = async () => {
    return await axios.get('http://localhost:3444/products').then(response => {
        console.log(response.data)
        return response.data
      }) 
}  

export const fetchProductApi = async (id) => {
  return await axios.get(`http://localhost:3444/product/${id}`).then(response => {
      console.log(response.data);
      return response.data
  })
}

export const fetchCategoriesApi = async () => {
  return await axios.get('http://localhost:3444/categories').then(response => {
    console.log(response.data);
    return response.data;
  })
}

export const userSingInApi = async (userLogin, userPassword) => {
  return await axios.post('http://localhost:3444/singIn', {
    login: userLogin,
    password: userPassword,
  }).then(response => {
    console.log(response.data);
    return response.data;
  });
}

export const userSingUpApi = async (userName,userLogin, userPassword, userEmail) => {
  return await axios.post('http://localhost:3444/singUp', {
    name: userName,
    login: userLogin,
    password: userPassword,
    email: userEmail
  }).then(response => {
    console.log(response.data);
    return response.data;
  });
}

export const userAcceptApi = async () => {
  return await axios.get('http://localhost:3444/userAccept').then(response => {
      // returning the data here allows the caller to get it through another .then(...)
      console.log(response.data)
      return response.data
    }) 
}  

export const userLogOutApi = async () => {
  return await axios.get('http://localhost:3444/userLogOut').then(response => {
    return response.data
  })
}

export const userUpdateApi = async (updatedUser) => {
  console.log(updatedUser)
  return await axios.patch(`http://localhost:3444/update/${updatedUser.id}`, updatedUser).then(response => {
    console.log(response.data)
    return response.data
  })
}


export const sendMailApi = async (userEmail, userName, userPhone, userMessage) => {
  console.log(userEmail, userMessage,userName, userPhone)
  return await axios.post('http://localhost:3444/send', {
    email: userEmail, 
    name: userName,
    phone: userPhone, 
    message: userMessage
  }).then(response => {
    console.log(response.data)
    return response.data
  });
}

export const orderApi = async (userName, userEmail, userCity, userPhone, price, products) => {
  console.log(userEmail, userName, userCity, userPhone, products, price)
  return await axios.post('http://localhost:3444/toOrder', {
    name: userName,
    email: userEmail,
    city: userCity,
    phone: userPhone,
    orderPrice: price,
    orderProducts: products
  }).then(response => {
    console.log(response.data)
    return response.data
  })
}