
import {
  ADD_TO_CART_FROM_DETAILS,
  REMOVE_FROM_CART,
  CLEAN_BASKET,
  UPDATE_AMOUNT,
  ADD_TO_CART,

} from '../../actionType'

import * as R from 'ramda'

export const addToBasket = (id, products) => dispatch => {
  var product = R.find(R.propEq('_id', id), products)
  dispatch({
    type: ADD_TO_CART,
    payload: product
  })
}

  
export const addToBasketFromProductDetails = (product, count) => dispatch => {
  var basket = {
    basketProduct: product,
    basketCount: count
  }

  dispatch({
    type: ADD_TO_CART_FROM_DETAILS,
    payload: basket
  })
}

export const removeFromBasket = product => dispatch => {
  dispatch({
    type: REMOVE_FROM_CART,
    payload: product
  })
}

export const updatePriceAction = (product ,count) => dispatch => {
  var updated = {
    updatedProduct: product,
    updatedCount: count
  }
  console.log(product)
  dispatch({
    type: UPDATE_AMOUNT,
    payload: updated
  })
}

export const cleanBasket = () => dispatch => {
  dispatch({
    type: CLEAN_BASKET,
    payload: []
  })
}
