import React, {Component} from 'react'
import {Link, Redirect} from 'react-router-dom'
import { connect } from 'react-redux'
import PasswordMask from 'react-password-mask'

import { userSingIn } from '../../actions'

class SingInGrid extends Component{
    state = {
        login: "",
        loginError: "",
        password: "",
        passwordError: "",
        error: "",
        isRemember: false,

        redirect: Boolean
      };
    
      change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
      };
      
    
      validate = () => {
        let isError = false;
    
        if (this.state.login.length < 5) {
          isError = true;
          this.setState({error: "    5 "})
        }
        else{
          this.setState({error: ""})
        }
    
        return isError;
      };

      onSubmit = e => {
        e.preventDefault();
    
        const err = this.validate();
        if (!err) {
            console.log(this.state.isRemember)
            this.props.userSingIn(this.state.login, this.state.password, this.state.isRemember)
            this.setState({
              login: "",
              loginError: "",
              password: "",
              passwordError: "",
            });
        }
        //console.log(this.props.redirectStatus)

  
        }
    
      renderRedirect = () => {
        if (this.props.redirectStatus === true) {
          return <Redirect to='/' />
        }
        else if(this.props.redirectStatus === false && this.state.error === "") {
          return <p className={'error'}>   </p>
        }
      }
    
    render(){
        return(
        <div className={'login-register-area pt-95 pb-100'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-lg-7 col-md-12 ml-auto mr-auto'}>
                        <div className={'login-register-wrapper'}>
                            <div className={'login-register-tab-list nav'}>
                                <Link className={'active'}  to={'/singIn'}>
                                    <h4>  </h4>
                                </Link>
                                <Link to={'/singUp'}>
                                    <h4>  </h4>
                                </Link>
                            </div>
                            <div className={'tab-content'}>
                                <div className={'tab-pane active'}>
                                    <div className={'login-form-container'}>
                                        <div className={'login-register-form'}>
                                            <form>
                                                {this.renderRedirect()}
                                                {this.state.error !== "" ? (<p className={'error'}>{this.state.error}</p>) : (false)}
                                                <input type={'text'} onChange={e => this.change(e)} name="login" value={this.state.login} placeholder={''} />
                                                <PasswordMask showButtonContent="" hideButtonContent="" onChange={e => this.change(e)} name="password" value={this.state.password} placeholder={''} />
                                                <div className={'button-box'}>
                                                    <div className={'login-toggle-btn'}>
                                                        <input type={'checkbox'} name="isRemember" onChange={e => this.change(e)}/>
                                                        <label> </label>
                                                        <Link to={'#'}> ?</Link>
                                                    </div>
                                                    <button onClick={e => this.onSubmit(e)}><span></span></button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

const mapStateToProps = store => {
    return {
      redirectStatus: store.userReducer.redirectStatus
    }
  }
  
  const mapDispatchToProps = {
    userSingIn
  }
  
  export default connect(
    mapStateToProps, mapDispatchToProps
  )(SingInGrid)