import React,{ Component } from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faChevronDown, faSignOutAlt } from '@fortawesome/free-solid-svg-icons'


import { fetchCategories, selectedCategorie, userSingOut, userAccept } from '../../actions'
import { Cart } from '../../containers'

class Header extends Component
{
    state = {
        collapse1: '',
    }

    componentDidMount() {
        this.props.fetchCategories()
        this.props.userAccept()
    }
    
    renderCategories = (categories) =>{
        return categories.map(element => (
            <li key={element.idCategorie}><Link onClick={this.props.selectedCategorie(element.name)} to={`/shop`}>{element.name}</Link></li>
        ))
    }
      
    toggleSingleCollapse = collapseId => () => {
        this.setState({
          [collapseId]: !this.state[collapseId]
        });
    };
    
    onBtnClick = (event) =>{
        this.props.userSingOut()
        window.location.reload(false);
    }

  render() {
    return(
      <header className={'header-area'}>
          <div className={'header-middle'}>
              <div className={'container'}>
                  <div className={'row'}>
                      <div className={'col-lg-3 col-md-4 col-12 col-sm-4'}>
                          <div className={'logo'}>
                              <Link to={'/'}>
                                  <img alt={''} src={'assets/img/logo/logo.png'}/>
                              </Link>
                          </div>
                      </div>
                      <div className={'col-lg-9 col-md-8 col-12 col-sm-8'}>
                          <div className={'header-middle-right f-right'}>
                              {this.props.redirectStatus === true ? 
                                ( 
                                <div className={'header-login'}>

                                    <Link to={'/account'}>
                                      <div className={'header-icon-style'}>
                                          <FontAwesomeIcon icon={faUser} size="3x"  />
                                      </div>
                                    <div className={'login-text-content'}>
                                    <p>: {this.props.user.login} <br/> 
                                    <span>: {this.props.user.name}</span> <br />
                                    </p>
                                    </div>
                                    </Link>
                                    <Link to="#" onClick={this.onBtnClick}> <FontAwesomeIcon icon={faSignOutAlt}/></Link>

                                </div>
                                ):
                                (
                                <div className={'header-login'}>
                                  <Link to={'/singIn'}>
                                      <div className={'header-icon-style'}>
                                          <FontAwesomeIcon icon={faUser} size="3x"  />
                                      </div>
                                        
                                      <div className={'login-text-content'}>
                                          <p> <br/> / <span></span></p>
                                      </div>
                                  </Link>
                                </div>
                                )}
                              <Cart/>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
          <div className={'header-bottom transparent-bar black-bg'}>
              <div className={'container'}>
                  <div className={'row'}>
                      <div className={'col-lg-12 col-md-12 col-12'}>
                          <div className={'main-menu'}>
                              <nav>
                                  <ul>
                                      <li className={'top-hover'}><Link to={'/'}></Link>
                                      </li>
                                      <li><Link to={'/about'}> </Link></li>
                                      <li className={'mega-menu-position top-hover'}><Link to={'/shop'}> <FontAwesomeIcon icon={faChevronDown} size="xs" style={{marginBottom: '2px', marginLeft: '3px'}}/></Link>
                                          <ul className={'mega-menu'}>
                                              <li>
                                                  <ul>
                                                      <li className={'mega-menu-title'}><Link to={'#'}></Link></li>
                                                      {this.renderCategories(this.props.categories)}
                                                  </ul>
                                              </li>
                                          </ul>
                                      </li>
                                      <li><Link to={'/stock'}>  </Link></li>
                                      <li><Link to={'/delivery'}></Link></li>
                                      <li><Link to={'/contacts'}></Link></li>
                                  </ul>
                              </nav>
                          </div>
                      </div>
                  </div>
              </div>
          </div>

    <div className={'mobile-menu-area'}>
      <div className={'container'}>
        <div className={'row'}>
          <div className={'col-lg-12'}>

          </div>
        </div>
      </div>
      </div>
      </header>
    );
  }
}


const mapStateToProps = store => {
    return {
        user: store.userReducer.user,
        redirectStatus: store.userReducer.redirectStatus,
        categories: store.categoriesReducer.categories
    }
}

const mapDispatchToProps = {
    fetchCategories,
    selectedCategorie,
    userSingOut,
    userAccept
}
  
export default connect(mapStateToProps,mapDispatchToProps)(Header)
  
  